body {
  margin: 40px 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ffe8d6;
  letter-spacing: 3px;
}
.App {
  max-width: 640px;
  margin: 0 auto;
}
nav {
  background: #b7b7a4;
  padding: 10px 20px;
  color: #fff;
  display: flex;
  align-items: center;
}
nav h1 {
  margin: 0;
  margin-right: auto;
  color:#6b705c;
}
nav li {
  display: inline-block;
  margin: 0 0 0 16px;
  cursor: pointer;
  color: #fff;
  font-size: 20px; 
}
nav a {
  color: #fff;
  text-decoration: none;
}
.memo-list ul {
  padding: 0;
}
.memo-list li {
  list-style-type: none;
  padding: 10px;
  background: #fff;
  margin: 10px 0;
  cursor: pointer;
}
label, label span {
  display: block;
  margin: 6px 0;
}
input {
  font-size: 20px;
  padding: 10px;
  border-radius: 3px;
  border: 0px none;
}
button {
  margin-top: 30px;
  padding: 10px;
  border: 0px none;
  border-radius: 3px;
  background: #b7b7a4;
  color: #ffe8d6;
  font-size: 20px;  
  text-decoration: none;
  cursor: pointer;
}
button:hover{
  border: 3px solid;
}